<template>
  <div>
    <div class="index-header">
      <div class="header">
        <Com-Header/>
      </div>
      <div class="slide">
        <img :src="caseDetail.phoneUrl" alt>
      </div>
    </div>
    <div class="detail-content">
      <div class="block-1 padding-default">
        <div>项目编号</div>
        <div class="line-3px"></div>
        <div v-text="caseDetail.customerName"></div>
        <div class="desc">
          <div>
            <p>项目：</p>
            <p v-text="caseDetail.projectName"></p>
          </div>
          <div>
            <p>分类：</p>
            <p v-text="caseDetail.industryInfoStr"></p>
          </div>
          <div>
            <p>服务内容：</p>
            <p v-text="caseDetail.serviceContentStr"></p>
          </div>
        </div>
      </div>

      <div class="article-content">
        <div v-for="(item,index) in caseDetail.articleContent" :key="index">
          <!-- <div v-if="item.type === 'title_project'" v-text="item.value" class="title-project"></div> -->

          <div
            v-if="item.type === 'title_details'"
            v-text="item.value"
            class="title-details padding-default"
          ></div>
          <div
            v-if="item.type === 'content_details' && !item.valueArr"
            v-text="item.value"
            class="content-details padding-default"
          ></div>
           <div
            v-if="item.type === 'content_details' && item.valueArr"
            
            class="content-details padding-default"
          >
            <p v-for="(items,index) in item.valueArr" :key="index" v-text="items"></p>
          </div>

           <div
            v-if="item.type === 'img_details'"
            class="img-details padding-default"
          >
            <img :src="item.value" alt="">
          </div>
        </div>
      </div>
    </div>

    <Com-Footer/>
  </div>
</template>

<script>
import ComHeader from "../components/ComHeader";
import ComFooter from "../components/ComFooter";

export default {
  name: "",
  data() {
    return {
      caseDetail: {}
    };
  },
  components: { ComHeader, ComFooter },

  computed: {},

  mounted() {
    let type = this.$router.currentRoute.query.type;
    this.getCaseDetail(type);
  },
  methods: {
    getCaseDetail(type) {
      let that = this;
      fetch(this.API.caseDetail + type)
        .then(response => response.json())
        .then(res=>{
          console.log(JSON.stringify(res, null, 2));
          if (res.code === "0000") {
            res.detail.industryInfoStr = res.detail.industryInfo.join("、");
            res.detail.serviceContentStr = res.detail.serviceContent.join("、");

            res.detail.articleContent.forEach(ele=>{
                let value = ele.value.split('\n');
                // if(value)
                console.log(value);
                if(value.length > 1){
                    ele.valueArr = value;
                }
            })

            that.caseDetail = res.detail;
          }
        })
        .catch(e => console.log("Oops, error", e));
    }
  }
};
</script>

<style scoped  lang="less">
.slide {
  img {
    width: 100%;
  }
}
.index-header {
  position: relative;
  .header {
    background: #333333;
  }
}

.padding-default {
  padding-left: 0.44rem;
  padding-right: 0.54rem;
}

.line-3px {
  margin-top: 0.14rem;
  margin-bottom: 0.14rem;
  height: 0.06rem;
  background: #333333;
}

.block-1 {
  font-size: 0.28rem;
  color: #222222;
  padding-top: 0.76rem;
  .desc {
    margin-top: 0.44rem;
    > div {
      margin-bottom: 0.2rem;
      p:last-child {
        margin-top: 0.12rem;
        font-size: 0.26rem;
        color: #999999;
      }
    }
  }
}

.article-content {
    margin-bottom: .6rem;
  .title-details {
    font-size: 0.32rem;
    color: #222222;
    margin-top: .6rem;
    margin-bottom: .2rem;
  }
  .content-details {
    font-size: 0.3rem;
    color: #4a4a4a;
    p{
        margin: .1rem 0;
    }
  }
  .img-details{
      margin-top: .36rem;
      img{
          width: 100%;
      }
  }
}
</style>



